.form-landing-splash-container {
  margin: 20% 0 5% 0;
  padding-right: 92px; }
  .form-landing-splash-container .mas-container {
    display: flex;
    align-items: center;
    margin: 100px 0 20px 0; }
    .form-landing-splash-container .mas-container .h7 {
      position: relative;
      z-index: 1;
      max-width: 167px;
      margin-left: 10px; }
  .form-landing-splash-container h1 {
    position: relative;
    z-index: 1;
    max-width: 300px; }
  .form-landing-splash-container .splash-img {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 256px; }

.form-landing-insurer-carousel .insurer-logo-carousel {
  margin-top: 36px; }
  .form-landing-insurer-carousel .insurer-logo-carousel .insurer-logo-sq {
    height: 60px; }
    .form-landing-insurer-carousel .insurer-logo-carousel .insurer-logo-sq img {
      object-fit: contain;
      width: 100px;
      height: 60px; }

.form-landing-insurer-carousel .owl-prev,
.form-landing-insurer-carousel .owl-next {
  position: absolute;
  top: 0 !important;
  color: transparent;
  width: 60px;
  height: 80px;
  transition: filter 0.2s ease;
  background-image: url("../../../assets/images/FormLanding/CarouselArrow.svg");
  background-repeat: no-repeat;
  background-position: top center; }
  .form-landing-insurer-carousel .owl-prev:before,
  .form-landing-insurer-carousel .owl-next:before {
    border: 0 !important; }
  .form-landing-insurer-carousel .owl-prev:hover,
  .form-landing-insurer-carousel .owl-next:hover {
    filter: invert(0.7) saturate(0); }

.form-landing-insurer-carousel .owl-prev {
  left: 0 !important;
  transform: translateX(-60px) translateY(-5px) !important;
  background-position: top center; }
  .form-landing-insurer-carousel .owl-prev:active {
    filter: invert(0.7) saturate(0);
    transform: translateX(-60px) translateY(-5px) scale(1.1) !important; }

.form-landing-insurer-carousel .owl-next {
  right: 0 !important;
  transform: translateX(60px) translateY(-5px) scaleX(-1) !important;
  background-position: top center; }
  .form-landing-insurer-carousel .owl-next:active {
    filter: invert(0.7) saturate(0);
    transform: translateX(60px) translateY(-20px) scale(-1.1) !important; }
