@media screen and (max-width: 575px) {
  .tokio-marine-xplora-japan-popup .modal-body {
    padding: 0; } }

.tokio-marine-xplora-japan-popup .banner {
  width: 100%; }
  .tokio-marine-xplora-japan-popup .banner img {
    width: 100%;
    height: auto; }
    @media screen and (max-width: 575px) {
      .tokio-marine-xplora-japan-popup .banner img {
        width: calc(100% + 70px);
        transform: translateX(-35px); } }

.tokio-marine-xplora-japan-popup .info-container {
  display: flex;
  margin: 12.5px 0 20px 0; }
  .tokio-marine-xplora-japan-popup .info-container .image {
    padding-right: 20px; }
    @media screen and (min-width: 992px) {
      .tokio-marine-xplora-japan-popup .info-container .image {
        transform: translateY(15px); } }
  .tokio-marine-xplora-japan-popup .info-container h4 {
    font-weight: 800;
    font-size: 14px;
    color: #2C3652; }
  .tokio-marine-xplora-japan-popup .info-container p {
    font-weight: normal;
    font-size: 12px;
    color: #6E7F95; }

.tokio-marine-xplora-japan-popup .button-container {
  max-width: 414px;
  display: flex;
  justify-content: center; }
  .tokio-marine-xplora-japan-popup .button-container .button-container-inner {
    padding: 5px 10px; }
