.promo-popup {
  margin: auto;
  padding-bottom: 0; }
  @media screen and (max-width: 576px) {
    .promo-popup {
      padding-left: 15px;
      padding-right: 15px; } }
  @media screen and (min-width: 576px) {
    .promo-popup {
      max-width: 500px;
      width: calc(100vw - 50px); } }
  .promo-popup .img-container,
  .promo-popup .main-text-container,
  .promo-popup .caption-text-container,
  .promo-popup .sub-text-container,
  .promo-popup .button-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column; }
    .promo-popup .img-container p,
    .promo-popup .main-text-container p,
    .promo-popup .caption-text-container p,
    .promo-popup .sub-text-container p,
    .promo-popup .button-container p {
      margin: 0;
      text-align: center; }
  .promo-popup .main-text-container {
    font-size: 28px;
    font-weight: bold;
    color: #00D7AF; }
    @media screen and (max-width: 576px) {
      .promo-popup .main-text-container {
        font-size: 21.6px; } }
  .promo-popup .caption-text-container {
    font-size: 18px;
    font-weight: bold;
    color: #46737A; }
    @media screen and (max-width: 576px) {
      .promo-popup .caption-text-container {
        font-size: 18px; } }
  .promo-popup .sub-text-container {
    margin-top: 14px;
    margin-bottom: 36px;
    font-size: 16px;
    font-weight: normal;
    color: #99BBC1; }
    @media screen and (max-width: 576px) {
      .promo-popup .sub-text-container {
        font-size: 14px; } }
  .promo-popup .button-container {
    justify-content: flex-start;
    height: 130px; }
    @media screen and (max-width: 576px) {
      .promo-popup .button-container button {
        max-width: 370px;
        min-width: auto !important; } }
  .promo-popup .tab-container {
    padding-top: 50px; }
  .promo-popup .img-container {
    max-width: 650px;
    margin: 10px auto; }
    .promo-popup .img-container img {
      max-width: 100%; }
