.landing-form-outer-container-new .circle-shape, .landing-form-outer-container-new .circle-green {
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 50%; }

.landing-form-outer-container-new {
  max-width: 1110px;
  padding: 0 40px 0 40px !important;
  margin: auto;
  position: relative;
  margin-top: 52px; }
  @media screen and (max-width: 575px) {
    .landing-form-outer-container-new {
      padding: 0 15px 100px 15px !important; } }
  @media screen and (max-width: 991px) {
    .landing-form-outer-container-new {
      margin-top: 0; } }
  .landing-form-outer-container-new .circle-shape {
    background-color: #F9FAFF; }
    .landing-form-outer-container-new .circle-shape:after {
      content: '';
      position: absolute;
      top: 10%;
      left: 10%;
      width: 80%;
      height: 80%;
      background-color: white;
      border-radius: 50%; }
  .landing-form-outer-container-new .circle-upper {
    width: 1200px;
    height: 1200px;
    transform: translateX(40%) translateY(-60%); }
  .landing-form-outer-container-new .circle-bottom {
    width: 2255px;
    height: 2255px;
    transform: translateX(-60%) translateY(448px); }
  .landing-form-outer-container-new .circle-green {
    width: 239px;
    height: 239px;
    background-color: rgba(0, 215, 175, 0.1);
    transform: translateX(250%) translateY(304px); }

@keyframes float {
  0% {
    transform: translateY(0px); }
  50% {
    transform: translateY(20px); }
  100% {
    transform: translateY(0px); } }
  .landing-form-outer-container-new .mouse {
    justify-content: center; }
    .landing-form-outer-container-new .mouse img {
      animation: float 3s ease-in-out infinite; }

.landing-form-container-new {
  max-width: 1006px; }
  .landing-form-container-new .edit-header {
    text-align: center;
    font-size: 24px;
    font-weight: bold;
    color: #354F53; }
  .landing-form-container-new .button-container {
    margin-top: 30px; }
    .landing-form-container-new .button-container .button-container-inner:nth-of-type(1) button {
      margin-right: 10px; }
      @media screen and (max-width: 575px) {
        .landing-form-container-new .button-container .button-container-inner:nth-of-type(1) button {
          margin-right: 0;
          margin-bottom: 5px; } }
    .landing-form-container-new .button-container .button-container-inner:nth-of-type(2) button {
      margin-left: 10px; }
      @media screen and (max-width: 575px) {
        .landing-form-container-new .button-container .button-container-inner:nth-of-type(2) button {
          margin-left: 0;
          margin-top: 5px; } }

.landing-form-container-border {
  background: white;
  border-radius: 10px;
  margin: auto;
  width: 100%; }
  @media screen and (min-width: 576px) {
    .landing-form-container-border {
      padding: 30px;
      box-shadow: 0px 50px 40px rgba(0, 0, 0, 0.05); } }
  @media screen and (min-width: 992px) {
    .landing-form-container-border {
      width: 458px;
      margin-left: -60px; } }
  @media screen and (min-width: 1180px) {
    .landing-form-container-border {
      margin-left: 0; } }
